import React from 'react';
import ReactCanvasNest from "react-canvas-nest";
import { InputItem, ApplyItem, ApplyWrapper, Title, ButtonItem,SubmitButton,ResetButton} from "./style";
import {Input} from "antd";
import {connect} from "react-redux";
import {Redirect} from 'react-router-dom';

import * as actionCreators from "../user/store/actionCreators";



class User extends React.Component{

    constructor(props){
        super(props);

    }

    render(){
        const {loginStatus}=this.props;
        if(loginStatus){
        return (
            <div>
                <ReactCanvasNest className = 'canvasNest' config = {{ pointColor: ' 255, 255, 255 ',lineColor:'255,255,255' }} style = {{ zIndex: 0 }} />
                <ApplyWrapper>
                    <Title><h1>修改用户信息</h1></Title>
                    <ApplyItem>
                        <InputItem>
                            <Input addonBefore="姓名"  disabled value={this.props.UserData.name}/>
                        </InputItem>

                        <InputItem>
                            <Input addonBefore="学号" allowClear value={this.props.UserData.stu_id} disabled/>
                        </InputItem>

                        <InputItem>
                            <Input addonBefore="班级" allowClear value={this.props.UserData.class}/>
                        </InputItem>

                        <InputItem>
                            <Input addonBefore="院系"  allowClear value={this.props.UserData.family}/>
                        </InputItem>

                        <InputItem>
                            <Input addonBefore="政治面貌" allowClear value={this.props.UserData.political_profile}/>
                        </InputItem>

                        <InputItem>
                            <Input addonBefore="性别" allowClear value={this.props.UserData.sex}/>
                        </InputItem>

                        <InputItem>
                            <Input addonBefore="联系方式"  allowClear value={this.props.UserData.contact}/>
                        </InputItem>

                        <InputItem>
                            <Input addonBefore="邮箱" allowClear value={this.props.UserData.email}/>
                        </InputItem>

                        <ButtonItem>
                            <SubmitButton>提交</SubmitButton>
                            <ResetButton>重置</ResetButton>

                        </ButtonItem>

                    </ApplyItem>
                </ApplyWrapper>
            </div>
        );}else{
            return <Redirect to='/login'/>
        }
    }

    componentDidMount(){
        this.props.getUser();

    }

}





const mapStateToProps =(state)=>{
    return {
        UserData:state.user.UserData,
        loginStatus:state.login.login,
    }
};


const mapDispatch = (dispatch)=>({
    getUser(){
        dispatch(actionCreators.getUser())

    }
});


export default connect(mapStateToProps,mapDispatch)(User);
